import React, { Component } from 'react';
import axios from 'axios';

const Expence_server = `createexpence/`;

class expences extends Component {
	constructor(props) {
		super(props);
		this.state = {
			date1: localStorage.getItem('date'),
			expenceName: '',
			expenceAmmount: '',
		};
	}

	submitExpence = () => {
		let dailyExpence = {
			date: this.state.date1,
			expenceName: this.state.expenceName,
			expenceAmmount: this.state.expenceAmmount,
		};
		axios.post(Expence_server, dailyExpence);
		this.clearData();
	};

	clearData = () => {
		this.setState({
			expenceName: '',
			expenceAmmount: '',
		});
	};

	setval = async (e) => {
		this.setState({
			expenceAmmount: e.target.value,
		});
	};

	componentDidMount() {
		this.setState({
			dailyExpence: {
				...this.state.dailyExpence,
				date: this.state.date1,
			},
		});
	}

	render() {
		return (
			<div>
				<div className='reports__expence'>
					<a
						style={{
							'width': '200px',
							'margin': '0px 0px 20px 0px',
							'padding': '10px',
							'border': 'solid thin black',
							'text-align': 'center',
							'textDecoration': 'none',
						}}
						href='/reports'>
						Back
					</a>
					<h3 style={{ 'font-size': '30px' }}>Expence</h3>
					<h3>Date :{this.state.date1}</h3>
					<div
						style={{
							display: 'flex',
							flexDirection: 'column',
							height: '200px',
							justifyContent: 'space-evenly',
						}}>
						Expence Name:{' '}
						<select
							name='expenceName'
							onChange={(e) =>
								this.setState({
									expenceName: e.target.value,
								})
							}>
							<option>Expense</option>
							<option>Tea</option>
							<option>FOOD</option>
							<option>PETROL/GAS</option>
							<option>SNACKS</option>
							<option>MEDICAL</option>
							<option>BIKE</option>
							<option>BIKE OIL CHANGE</option>
							<option>BIKE TIRE CHANGE</option>
							<option>BIKE CHAIN</option>
							<option>OTHERS</option>
						</select>
						<input
							type='text'
							placeholder='Ammount'
							onChange={this.setval}
							value={this.state.expenceAmmount}></input>
						<button onClick={this.submitExpence}>Add Expence 💾</button>
					</div>
				</div>
			</div>
		);
	}
}

export default expences;
