import React, { useState } from 'react';

export default function RefreshComponent() {
	const [storageData, setStorageData] = useState([
		{
			date: localStorage.getItem('date'),
			expence: localStorage.getItem('expence'),
			intrest: localStorage.getItem('intrest'),
			company: localStorage.getItem('company'),
			loan: localStorage.getItem('loan'),
			aih: localStorage.getItem('aih'),
			ms: localStorage.getItem('ms'),
			dl: localStorage.getItem('dl'),
		},
	]);
	const [loginTokens, setLoginTokens] = useState([
		{
			token: sessionStorage.getItem('access_token'),
			login: sessionStorage.getItem('login'),
		},
	]);

	const setValuestoStorage = async () => {
		console.log(storageData);
		await localStorage.setItem('date', storageData.date);
		await localStorage.setItem('expence', storageData.expence);
		await localStorage.setItem('intrest', storageData.intrest);
		await localStorage.setItem('company', storageData.company);
		await localStorage.setItem('loan', storageData.loan);
		await localStorage.setItem('aih', storageData.aih);
		await localStorage.setItem('ms', storageData.ms);
		await localStorage.setItem('dl', storageData.dl);
		await sessionStorage.setItem('access_token', loginTokens.token);
		await sessionStorage.setItem('login', loginTokens.token);
	};

	const setValue = async () => {
		await setStorageData([
			{
				date: localStorage.getItem('date'),
				expence: localStorage.getItem('expence'),
				intrest: localStorage.getItem('intrest'),
				company: localStorage.getItem('company'),
				loan: localStorage.getItem('loan'),
				aih: localStorage.getItem('aih'),
				ms: localStorage.getItem('ms'),
				dl: localStorage.getItem('dl'),
			},
		]);
		await setLoginTokens([
			{
				token: sessionStorage.getItem('access_token'),
				login: sessionStorage.getItem('login'),
			},
		]);
	};

	const refresh = async () => {
		await setValue();
		await localStorage.clear();
		await sessionStorage.clear();
		caches.keys().then((names) => {
			names.forEach((name) => {
				caches.delete(name);
			});
		});
		// cookies.remove('user');
		// await cookie.clear();
		// await store.clear();
		// await setValuestoStorage();
		await window.location.reload();
	};

	return (
		<button className='btn btn-dark' onClick={refresh}>
			Refresh
		</button>
	);
}
