import React, { useState, useEffect } from 'react';
import axios from 'axios';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import './collectionDetails.css';

const authKey = sessionStorage.getItem('access_token');

const CollectionList_Server = `getcollection/${authKey}/`;
const updateCostumerserver = `updatecostumer/`;
const updateCollectionList_Server = `updatecollection/`;
const deleteCollectionList_Server = `deletecollection/`;
const DL_SERVER = `getdl/`;
const createDL_SERVER = `createdl`;
const getCostumerServer = `getcostumer/${authKey}/`;
const updateDL_SERVER = `updatedl`;

export default class collectionDetails extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			date1: '',
			dl: 0,
			searchTerm: '',
			collectionData: [],
			updated_costumerRecords: {
				name: 'Name',
				loanammount: '',
				ammount_Balance: '',
				loan_date: '',
				loanIntrest: '',
				ms: '',
				place: '',
				place_id: '',
			},
			editCostumerID: '',
			editCollectionID: '',
			editdate: '',
			editcostumer_name: '',
			editammount: 'Ammount',
			collectedAmmount: '',
			editcostumerid: '',
			updated_costumerRecords1: {
				name: '',
				loanammount: '',
				ammount_Balance: '',
				loan_date: '',
				loanIntrest: '',
				ms: '',
				place: '',
				place_id: '',
			},
			updatedCollectionrecords: {
				date: '',
				costumer_id: '',
				costumer_name: '',
				ammount: '',
			},
		};
	}

	CalculateDL = async () => {
		let date = this.state.date1;
		let collections;
		let dl = 0;
		await axios
			.get(CollectionList_Server)
			.then((res) => (collections = res.data));
		let exactData = collections.filter((data) => data.date.includes(date));
		await exactData.map((data) => (dl = parseInt(data.ammount) + parseInt(dl)));
		await this.setState({
			dl,
		});
	};

	deleteCollection = async (id) => {
		axios.delete(`${deleteCollectionList_Server}${id}`);
		this.getCostumerdata();
	};

	updateDL = async () => {
		let date = this.state.date1;
		let dldata;
		await axios
			.get(DL_SERVER)
			.then(
				(res) =>
					(dldata = res.data.filter((data1) => data1.date.includes(date))),
			);

		let dataPost = await {
			date: date,
			ammount: this.state.dl,
		};

		let dlid;

		console.log(dldata);

		if (dldata.length === 0) {
			axios.post(`${createDL_SERVER}/`, dataPost);
		} else {
			dldata.map((data2) => {
				return (dlid = data2.id);
			});
			axios.post(`${updateDL_SERVER}/${dlid}/`, dataPost);
		}
	};

	changeDate = async (e) => {
		await this.setState({
			date1: e.target.value,
		});
		var date = new Date(this.state.date1);
		console.log(date);
		await this.CalculateDL();
		await this.getCostumerdata();
	};

	componentDidMount() {
		this.callfunction();
	}

	callfunction = async () => {
		// await this.setState({
		// date1: localStorage.getItem("date"),
		// });
		console.log(this.state.date1);
		await this.CalculateDL();
		await this.getCostumerdata();
	};

	setsearchCostumer = (e) => {
		this.setState({
			searchTerm: e.target.value,
		});
	};

	editCollection = async (details, costumer_id) => {
		let costumersRecords;
		let collections;
		await axios.get(getCostumerServer).then((res) => {
			costumersRecords = res.data;
		});

		var searchdata;

		await costumersRecords.map((costumerdata) => {
			if (costumerdata.id === costumer_id) {
				searchdata = costumerdata;
			}
		});

		this.setState({
			updated_costumerRecords: {
				name: searchdata.name,
				loanammount: searchdata.loanammount,
				ammount_Balance: searchdata.ammount_Balance,
				loan_date: searchdata.loan_date,
				loanInterst: searchdata.loanInterst,
				ms: searchdata.ms,
				place: searchdata.place,
				place_id: searchdata.place_id,
			},
			editCostumerID: searchdata.id,
		});
		await axios.get(CollectionList_Server).then((res) => {
			return (collections = res.data);
		});

		var searchCollectiondata;

		await collections.map((data) => {
			if (data.id === details.id) {
				searchCollectiondata = data;
			}
		});

		await this.setState({
			editdate: searchCollectiondata.date,
			editcostumer_name: searchCollectiondata.costumer_name,
			editammount: searchCollectiondata.ammount,
			editCollectionID: searchCollectiondata.id,
			collectedAmmount: searchCollectiondata.ammount,
			editCostumerID: details.costumer_id,
		});
	};

	updateCollection = async () => {
		let costumerAmmount_balance =
			this.state.updated_costumerRecords.ammount_Balance;
		if (this.state.editammount > this.state.collectedAmmount) {
			let diff =
				parseInt(this.state.editammount) -
				parseInt(this.state.collectedAmmount);
			costumerAmmount_balance =
				parseInt(costumerAmmount_balance) - parseInt(diff);
		} else {
			let diff =
				parseInt(this.state.collectedAmmount) -
				parseInt(this.state.editammount);
			costumerAmmount_balance =
				parseInt(costumerAmmount_balance) + parseInt(diff);
		}
		costumerAmmount_balance.toString();
		await this.setState({
			updatedCollectionrecords: {
				...this.state.updatedCollectionrecords,
				date: this.state.editdate,
				costumer_id: this.state.editCostumerID,
				costumer_name: this.state.editcostumer_name,
				ammount: this.state.editammount,
			},

			updated_costumerRecords1: {
				name: this.state.updated_costumerRecords.name,
				loanammount: this.state.updated_costumerRecords.loanammount,
				loan_date: this.state.updated_costumerRecords.loan_date,
				loanInterst: this.state.updated_costumerRecords.loanInterst,
				ms: this.state.updated_costumerRecords.ms,
				place: this.state.updated_costumerRecords.place,
				ammount_Balance: costumerAmmount_balance.toString(),
				place_id: this.state.updated_costumerRecords.place_id,
			},
		});
		console.log(this.state.updated_costumerRecords1);
		await axios.post(
			`${updateCollectionList_Server}${this.state.editCollectionID}`,
			this.state.updatedCollectionrecords,
		);

		await axios.post(
			`${updateCostumerserver}${this.state.editCostumerID}/`,
			this.state.updated_costumerRecords1,
		);
		this.getCostumerdata();
		this.CalculateDL();
	};

	editCollectionFunction = (e) => {
		this.setState({
			editammount: e.target.value,
		});
	};

	getCostumerdata = async (e) => {
		let date = await this.state.date1;
		let costumerList;
		await axios.get(CollectionList_Server).then((res) => {
			costumerList = res.data;
		});
		var exactData = await costumerList.filter((data1) =>
			data1.date.includes(date),
		);
		await this.setState({
			collectionData: exactData,
		});
	};

	searchCostumerData = async () => {
		let collections;
		await axios.get(CollectionList_Server).then((res) => {
			return (collections = res.data);
		});
		var searchdata = collections.filter((collectionlist) =>
			collectionlist.costumer_name
				.toLowerCase()
				.includes(this.state.searchTerm.toLowerCase()),
		);
		let exactData = searchdata.filter((data1) =>
			data1.date.includes(this.state.date1),
		);
		this.setState({
			collectionData: exactData,
		});
	};

	render() {
		return (
			<div className='collectionDetails__'>
				<div className='d-flex flex-row gap-4 bg-light p-3 align-middle'>
					<div className='d-flex flex-row gap-2'>
						<h4>Date</h4>
						<TextField
							type='date'
							onChange={this.changeDate}
							InputLabelProps={{
								shrink: true,
							}}
							className='h-25'
							value={this.state.date1}
						/>
					</div>
					<div>
						<h4>DL Ammount: {this.state.dl}</h4>
					</div>
					<input
						type='text'
						id='outlined-search'
						style={{
							marginLeft: '20px',
							outline: 'none',
							flex: '0.9',
						}}
						onChange={this.setsearchCostumer}
						placeholder='Enter Costumer Name'
					/>
					<button
						className='btn btn-primary'
						style={{ margin: 7 }}
						onClick={this.searchCostumerData}>
						🔍 Search
					</button>
				</div>
				<div className='d-flex gap-3 p-10'>
					<div className='p-3 table-wrapper-scroll-y my-custom-scrollbar'>
						<table className='table table-bordered border-dark'>
							<thead className='thead-dark'>
								<tr>
									<th>Costumer Name</th>
									<th>Ammount Collected</th>
									<th>Edit</th>
								</tr>
							</thead>
							<tbody className='collectionDetails__collectiontr'>
								{this.state.collectionData.map((data) => {
									return (
										<tr key={data.id}>
											<td>{data.costumer_name}</td>
											<td>{data.ammount}</td>
											<td>
												<button
													onClick={() =>
														this.editCollection(data, data.costumer_id)
													}
													className='btn btn-danger'>
													Edit
												</button>
											</td>
											<td>
												<button onClick={() => this.deleteCollection(data.id)}>
													Del
												</button>
											</td>
										</tr>
									);
								})}
							</tbody>
						</table>
					</div>
					<div className='d-flex flex-column gap-2'>
						<TextField
							style={{ margin: 7 }}
							value={this.state.updated_costumerRecords.name}
						/>
						<TextField
							style={{ margin: 7 }}
							value={this.state.editammount}
							onChange={this.editCollectionFunction}
						/>
						<Button
							variant='contained'
							color='secondary'
							style={{ margin: 7 }}
							onClick={this.updateCollection}>
							Update Collections
						</Button>
						<Button
							variant='contained'
							color='secondary'
							style={{ margin: 7 }}
							onClick={this.updateDL}>
							Update DL ✔✔
						</Button>

						<a className='btn btn-danger' href='/costumercollection'>
							Get Costumer Records
						</a>
					</div>
				</div>
			</div>
		);
	}
}

