import React, { useState, useEffect } from 'react';
import axios from 'axios';

const host = 'http://localhost:8000';
const authKey = sessionStorage.getItem('access_token');
const CollectionList_Server = `${host}/getcostumercollectionbydate/${authKey}/`;
const CostumerDetail__Server = `${host}/costumerdetail/`;
const createCollectionList_Server = `${host}/createcollection/${authKey}/`;
const updateCostumerDetails_Server = `${host}/updatecostumer/`;

const date = localStorage.getItem('date');

const UpdateBalanceComponent = () => {
	const [newAmmount, setNewAmmount] = useState(0);
	const [costumer__id, setCostumer__id] = useState(
		sessionStorage.getItem('costumer__id'),
	);
	const [collectionData, setCollectionData] = useState([]);
	const [costumerData, setCostumerData] = useState([]);

	useEffect(() => {
		setCostumer__id(sessionStorage.getItem('costumer__id'));
	}, []);

	const getCollection = async () => {
		await axios
			.get(`${CollectionList_Server}${costumer__id}/${date}`)
			.then((res) => {
				setCollectionData(res.data);
			});
	};

	const getCostumer = async () => {
		await axios.get(`${CostumerDetail__Server}${costumer__id}/`).then((res) => {
			setCostumerData(res.data);
		});
	};

	const updateCollectionBalance = async () => {
		const { ammount, costumer_id, costumer_name, date } =
			await collectionData[0];
		var Newbalance;
		if (newAmmount > ammount) {
			let diff = parseInt(newAmmount) - parseInt(ammount);
			Newbalance = parseInt(costumerData.ammount_Balance) - parseInt(diff);
		} else if (newAmmount < ammount) {
			let diff = parseInt(ammount) - parseInt(newAmmount);
			Newbalance = parseInt(costumerData.ammount_Balance) + parseInt(diff);
		} else {
			console.log('nothing changed');
		}
		const costumerDataClone = await {
			name: costumerData.name,
			loanammount: costumerData.loanammount,
			loan_date: costumerData.loan_date,
			loanInterst: costumerData.loanInterst,
			ms: costumerData.ms,
			place: costumerData.place,
			ammount_Balance: Newbalance.toString(),
			place_id: costumerData.place_id,
		};
		const collection = await {
			date: date,
			costumer_id: costumer_id,
			costumer_name: costumer_name,
			ammount: parseInt(newAmmount),
		};
		await axios.post(createCollectionList_Server, collection).then(() => {
			axios
				.post(
					`${updateCostumerDetails_Server}${costumer__id}/`,
					costumerDataClone,
				)
				.then(() => {
					alert('Costumer Updated');
				});
		});
		await getCostumer();
		await getCollection();
		await sessionStorage.removeItem('costumer__id');
	};

	useEffect(async () => {
		await getCollection();
		await getCostumer();
		await console.log(costumer__id);
	}, []);

	return (
		<div className='d-flex flex-row gap-2'>
			{collectionData.map((data) => (
				<div className='d-flex flex-column gap-3 h-100 w-100 '>
					{/* <p>id: {id}</p> */}
					<input
						className='form-control w-auto'
						defaultValue={data.costumer_name}
					/>
					<input
						type='number'
						className='form-control w-auto'
						defaultValue={data.ammount}
						onChange={(e) => setNewAmmount(e.target.value)}
					/>
					<input
						type='date'
						className='form-control w-auto'
						defaultValue={data.date}
					/>
					<button onClick={updateCollectionBalance} className='btn btn-primary'>
						Update
					</button>
				</div>
			))}
			<div className='d-flex flex-column gap-3 h-100 w-100'>
				<p className='fs-4'>Balance</p>
				<input
					type='number'
					className='form-control w-auto'
					defaultValue={costumerData.ammount_Balance}
				/>
			</div>
		</div>
	);
};

export default UpdateBalanceComponent;
