import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './addcostumer.css';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';

import { Link } from 'react-router-dom';

const authKey = sessionStorage.getItem('access_token');
const createCostumer_server = `createcostumer/${authKey}/`;
const getPlaces = `places/`;

const AddCostumer = () => {
	const [name, setName] = useState('');
	const [loanammount, setLoanammount] = useState(0);
	const [loanIntrest, setLoanIntrest] = useState(0);
	const [loan_date, setLoan_date] = useState('');
	const [ms, setMs] = useState(0);
	const [place, setPlace] = useState('');
	const [ammount_Balance, setAmmount_Balance] = useState(0);
	const [place_id, setPlace_id] = useState('');
	const [showAlert, setShowAlert] = useState(false);
	const [places, setPlaces] = useState([]);

	const showToast = () => {
		if (showAlert === false) {
			setShowAlert(true);
		} else {
			setShowAlert(false);
		}
	};

	const setStateValue = (e) => {
		e.preventDefault();
		let CostumerInput = {
			name: name,
			loanammount: loanammount,
			loan_date: loan_date,
			loanInterst: loanIntrest,
			ms: ms,
			place: place,
			ammount_Balance: ammount_Balance,
			place_id: place_id,
		};
		savecostumerData(CostumerInput);
	};

	const getPlacesData = async () => {
		const response = await axios.get(getPlaces);
		setPlaces(response.data);
	};

	useEffect(() => {
		getPlacesData();
	}, []);

	const savecostumerData = async (UserData) => {
		axios.post(createCostumer_server, UserData).then(
			(result) => {
				setShowAlert(true);
			},
			(error) => {
				console.log(error, 'error');
			},
		);
	};

	const updatePlace = async (e) => {
		const response = await axios.get(`${getPlaces}${place_id}`);
		await setPlace(response.data.place);
	};

	useEffect(() => {
		updatePlace();
	}, [place_id]);

	return (
		<div className='w-100 d-flex align-middle justify-content-center'>
			<form className='shadow-sm d-flex w-25 p-3 flex-column gap-2'>
				{showAlert && (
					<div className='rounded d-flex justify-content-center align-middle flex-row'>
						<p className='badge text-dark text-white bg-dark'>
							Costumer Added Successfully..
							<button onClick={showToast} className='btn'>
								❌
							</button>
						</p>
					</div>
				)}
				<input
					type='text'
					onChange={(e) => {
						setName(e.target.value);
					}}
					placeholder='Costumer Name'
					className='form-control'
					name='name'
				/>
				<input
					type='number'
					onChange={(e) => {
						setLoanammount(e.target.value);
						setAmmount_Balance(e.target.value);
						setLoanIntrest(e.target.value / 10);
					}}
					placeholder='Loan Ammount'
					className='form-control'
					name='loanammount'
				/>
				<input
					type='number'
					onChange={(e) => {
						setAmmount_Balance(e.target.value);
					}}
					value={ammount_Balance}
					placeholder='Ammount Balance'
					className='form-control'
					name='ammount_Balance'
				/>
				<input
					type='date'
					className='form-control'
					onChange={(e) => {
						setLoan_date(e.target.value);
					}}
				/>
				<input
					type='text'
					onChange={(e) => {
						setLoanIntrest(e.target.value);
					}}
					value={loanIntrest}
					placeholder='Loan Intrest'
					className='form-control'
					name='loanInterst'
				/>
				<input
					type='text'
					onChange={(e) => {
						setMs(e.target.value);
					}}
					placeholder='Loan MS'
					className='form-control'
					name='ms'
				/>
				<select
					className='form-select'
					onChange={(e) => setPlace_id(e.target.value)}>
					{places.map((data) => (
						<option className='btn' value={data.place_id}>
							{data.place}
						</option>
					))}
				</select>
				<p>Place: {place}</p>
				{/* <input
					type='text'
					onChange={(e) => {
						setPlace(e.target.value);
					}}
					defaultValue={place}
					placeholder='Place'
					className='form-control'
					name='place'
				/>
				<input
					type='text'
					onChange={(e) => {
						setPlace_id(e.target.value);
					}}
					defaultValue={place_id}
					className='form-control'
					placeholder='Place Id'
					name='place_id'
				/> */}
				<button className='btn btn-primary' onClick={setStateValue}>
					Save Costumer Data
				</button>
				<Link className='btn btn-primary' to='/updateCostumer'>
					Update Costumers
				</Link>
			</form>
		</div>
	);
};

export default AddCostumer;
