import React, { useState, useEffect, useRef } from 'react';
import TextField from '@material-ui/core/TextField';
import Modal from '@material-ui/core/Modal';
import axios from 'axios';
import './addCollection.css';
import UpdateBalanceComponent from '../updateBalance/updatebalance';

const host = 'http://localhost:8000';

const authKey = sessionStorage.getItem('access_token');
const CostumerDetails_Server = `${host}/getcostumer/${authKey}/`;
const CollectionList_Server1 = `${host}/getcollection/${authKey}/`;
const updateCostumerDetails_Server = `${host}/updatecostumer/`;
const createCollectionList_Server = `${host}/createcollection/${authKey}/`;
const SearchCostumer = `${host}/costumerdetail/`;
const CostumerDetail__Server = `${host}/costumerdetail/`;
const CollectionList_Server = `${host}/getcostumercollectionbydate/${authKey}/`;

const clamp = (min, max, val) => Math.max(min, Math.min(val, max));

export default function AddCollection() {
	const [loadingState, setLoadingState] = useState(true);
	const [collectedammout, setCollectedammount] = useState('');
	const [costumerDetails, setCostumerDetails] = useState([
		{
			name: '',
			loanammount: '',
			loan_date: '',
			loanInterst: '',
			ms: '',
			place: '',
			ammount_Balance: 'updated_balance',
			place_id: '',
		},
	]);
	const [date1, setDate1] = useState('');
	const [searchTerm, setSearchTerm] = useState('');
	const inputRefs = useRef([]);
	const [collectionStateData, setcollectionStateData] = useState('0.00');
	const [day2, day22] = useState('0.00');
	const [day3, day33] = useState('0.00');
	const [c_name, setName] = useState('Costumer name');
	const [modelopen, setModelOpen] = useState(false);
	const [sortby, setSortBy] = useState('place');

	const changeDate = async (e) => {
		setDate1(e.target.value);
	};

	const changevalue = (e) => {
		setCollectedammount(e.target.value);
	};

	const changeCollection = async (e, details, id) => {
		const event = e;
		if (event.key === 'Enter') {
			let updated_balance =
				parseInt(details.ammount_Balance) - parseInt(collectedammout);
			let records = {
				name: details.name,
				loanammount: details.loanammount,
				loan_date: details.loan_date,
				loanInterst: details.loanInterst,
				ms: details.ms,
				place: details.place,
				ammount_Balance: updated_balance,
				place_id: details.place_id,
			};
			let collection = {
				date: date1,
				costumer_id: details.id,
				costumer_name: details.name,
				ammount: parseInt(collectedammout),
			};

			// await console.log(collection)

			let data_of_costumer = '';

			// await collection_records_bulk.filter(data => {
			//     return data_of_costumer =  data.costumer_id.toString().includes(details.id.toString())
			// })

			// if (data_of_costumer !== null) {
			//     console.error("exists", data_of_costumer)
			// }

			// getCostumerDetails()
			// await console.log(details.id, details.name);
			// await axios
			// 	.get(
			// 		`${getcostumercollectionbydate}${details.id}/${localStorage.getItem(
			// 			'date',
			// 		)}/`,
			// 	)
			// 	.then(async (res) => {
			// 		await setTodayCollection(res.data);
			// 		await console.log(res.data);
			// 	});
			// await getCollectionRecordsToday(details.id);
			// await console.log(todayCollection);

			await axios.post(createCollectionList_Server, collection).then(
				(res) => {
					if (res.data === 'Collection Updated') {
					} else {
						axios
							.post(`${updateCostumerDetails_Server}${details.id}/`, records)
							.then(
								(res) => {
									console.log('updated');
								},
								(err) => {
									console.log('error put', id);
								},
							);
					}
				},
				(error) => {
					console.log(error, 'error');
				},
			);

			const nextIndex = (await id) + 1;
			(await inputRefs.current[nextIndex]) &&
				inputRefs.current[nextIndex].focus();
		}
	};

	const getCostumerDetails = async () => {
		axios.get(CostumerDetails_Server).then((res) => {
			setCostumerDetails(res.data);
			setLoadingState(false);
		});
	};

	const getCurrentDate = async () => {
		let date1 = new Date();
		let month = date1.getMonth();
		let date = date1.getDate();
		if (month < 10) {
			month = parseInt(month) + 1;
			month = '0' + month;
		}
		if (date < 10) {
			date = parseInt(date);
			date = '0' + date;
		}
		let prevDate = date1.getFullYear() + '-' + month + '-' + date;
		await setDate1(prevDate);
		localStorage.setItem('date', prevDate);
	};

	const searchCostumerData = async () => {
		let collections;
		await axios.get(CostumerDetails_Server).then((res) => {
			return (collections = res.data);
		});
		var searchdata = await collections.filter((collectionlist) =>
			collectionlist.name.includes(searchTerm),
		);
		setCostumerDetails(searchdata);
	};

	useEffect(() => {
		setLoadingState(true);
		getCostumerDetails();
		getCurrentDate();
	}, []);

	const handleFocus = async (e, id) => {
		await setCollectedammount(e.target.value);
		await e.target.select();
		let x = await getprevcollection(id);
		let set = `${x[1]} [${x[0]}]`;
		await setcollectionStateData(set);
		let set1 = `${x[3]} [${x[2]}]`;
		await day22(set1);
		let set2 = `${x[5]} [${x[4]}]`;
		await day33(set2);
	};

	async function getprevcollection(costumerid) {
		let collectionlist = [];
		let newlist = [];
		await axios.get(`${CollectionList_Server1}`).then((res) => {
			collectionlist.push(
				res.data.filter(
					(data) => data.costumer_id.toString() === costumerid.toString(),
				),
			);
		});
		await collectionlist.map((data) => {
			if (data[data.length - 1] !== undefined) {
				const name = data[data.length - 1].costumer_name;
				newlist.push(data[data.length - 1].ammount);
				newlist.push(data[data.length - 1].date);
				setName(name);
			} else {
				newlist[0] = '0.00';
				newlist[1] = '00/00/000';
			}
		});
		await collectionlist.map((data) => {
			if (data[data.length - 2] !== undefined) {
				newlist.push(data[data.length - 2].ammount);
				newlist.push(data[data.length - 2].date);
			} else {
				newlist[2] = '0.00';
				newlist[3] = '00/00/000';
			}
		});
		await collectionlist.map((data) => {
			// console.log(, 'data');
			if (data[data.length - 3] !== undefined) {
				console.log('df');
				newlist.push(data[data.length - 3].ammount);
				newlist.push(data[data.length - 3].date);
				setName(data[data.length - 1].costumer_name);
			} else {
				newlist[4] = '0.00';
				newlist[5] = '0.00';
				// setName('Costumer Name');
			}
		});
		return newlist;
	}

	let date;

	function addDays(date) {
		let now = new Date();
		const copy = new Date(Number(date));
		copy.setDate(date.getDate() + 100);
		var copyDate = new Date(copy);
		if (now > copyDate) {
			return true;
		}
		return false;
	}

	var copyDate1 = '';

	function subDays() {
		let now = new Date();
		let copy = new Date(Number(now));
		copy.setDate(now.getDate() - 1);
		copyDate1 = new Date(copy);
	}

	let day0 = '';

	const setModelUID = async (id) => {
		await sessionStorage.setItem('costumer__id', id);
		await setModelOpen(true);
	};

	const SortById = costumerDetails
		.sort((a, b) => {
			if (a.id < b.id) return -1;
			if (a.id > b.id) return 1;
			return 0;
		})
		.map((details, id) => {
			date = new Date(details.loan_date);
			date = addDays(date);
			return (
				<tr key={id}>
					<td>
						<p className={date === false ? 'text-primary' : 'text-danger'}>
							{details.name}
						</p>
					</td>
					<td>
						<p>
							{details.place.length < 15
								? details.place
								: details.place.slice(0, 8) + '...'}
						</p>
					</td>
					<td>
						<p>{details.loanammount}</p>
					</td>
					<td>
						<p>{details.ammount_Balance}</p>
					</td>
					<td>
						<TextField
							label='Ammount'
							inputProps={{
								onKeyPress: (e) => changeCollection(e, details, id),
							}}
							onChange={changevalue}
							inputRef={(ref) => (inputRefs.current[id] = ref)}
							onFocus={(e) => handleFocus(e, details.id)}
							color='secondary'
							style={{
								margin: '10px',
							}}
							defaultValue={details.loanammount / 100}
						/>
					</td>
					<td className='p-3'>
						<button
							className='btn btn-outline-primary'
							onClick={() => {
								setModelUID(details.id);
							}}>
							📝
						</button>
					</td>
				</tr>
			);
		});

	const SortByPlace = costumerDetails
		.sort((a, b) => {
			if (a.place_id < b.place_id) return -1;
			if (a.place_id > b.place_id) return 1;
			return 0;
		})
		.map((details, id) => {
			date = new Date(details.loan_date);
			date = addDays(date);
			return (
				<tr key={id}>
					<td>
						<p className={date === false ? 'text-primary' : 'text-danger'}>
							{details.name}
						</p>
					</td>
					<td>
						<p>
							{details.place.length < 15
								? details.place
								: details.place.slice(0, 8) + '...'}
						</p>
					</td>
					<td>
						<p>{details.loanammount}</p>
					</td>
					<td>
						<p>{details.ammount_Balance}</p>
					</td>
					<td>
						<TextField
							label='Ammount'
							inputProps={{
								onKeyPress: (e) => changeCollection(e, details, id),
							}}
							onChange={changevalue}
							inputRef={(ref) => (inputRefs.current[id] = ref)}
							onFocus={(e) => handleFocus(e, details.id)}
							color='secondary'
							style={{
								margin: '10px',
							}}
							defaultValue={details.loanammount / 100}
						/>
					</td>
					<td className='p-3'>
						<button
							className='btn btn-outline-primary'
							onClick={() => {
								setModelUID(details.id);
							}}>
							📝
						</button>
					</td>
				</tr>
			);
		});

	return (
		<div className={'addCollection_'}>
			{modelopen === true ? (
				<div
					className='position-fixed h-50 w-50 gap-3 bg-light border p-3'
					style={{ zIndex: 100 }}>
					<UpdateBalanceComponent />
					<button
						className='btn btn-primary'
						style={{ marginTop: '10px' }}
						onClick={() => {
							sessionStorage.removeItem('costumer__id');
							setModelOpen(false);
						}}>
						Close
					</button>
				</div>
			) : null}
			<div className={'d-flex gap-5'}>
				<TextField
					type='date'
					name='collection date'
					onChange={changeDate}
					InputLabelProps={{
						shrink: true,
					}}
					value={date1}
				/>
				<input
					type='text'
					className='form-control w-50'
					onChange={(e) => setSearchTerm(e.target.value)}
					placeholder='Enter Costumer Name'
				/>
				<button className='btn btn-outline-danger' onClick={searchCostumerData}>
					Search
				</button>
			</div>

			<div>
				<div style={{ width: 'auto', display: 'flex', margin: 10 }}>
					<br />
					<p className='btn btn-warning' style={{ margin: 5 }}>
						{c_name}
					</p>{' '}
					<p className='btn btn-primary' style={{ margin: 5 }}>
						{' '}
						{collectionStateData}{' '}
					</p>
					<p className='btn btn-primary' style={{ margin: 5 }}>
						{day2}
					</p>
					<p className='btn btn-primary' style={{ margin: 5 }}>
						{day3}
					</p>
					<button
						className='btn btn-dark'
						onClick={() => setSortBy('id')}
						style={{ margin: 5 }}>
						Sort By Costumer Id
					</button>
					<button
						className='btn btn-dark'
						onClick={() => setSortBy('place')}
						style={{ margin: 5 }}>
						Sort By Place
					</button>
				</div>
				<div className='table-wrapper-scroll-y my-custom-scrollbar'>
					<table className={'table table-bordered'} style={{ margin: 10 }}>
						<thead className='thead-dark'>
							<tr className='p-3'>
								<th>Name</th>
								<th>Palace</th>
								<th>Loan</th>
								<th>Balance</th>
								<th>Ammount</th>
								<th></th>
							</tr>
						</thead>
						<tbody>
							{sortby === 'id' ? (
								loadingState === false ? (
									SortById
								) : (
									<div class='spinner-border text-primary' role='status'>
										<span class='sr-only'></span>
									</div>
								)
							) : loadingState === false ? (
								SortByPlace
							) : (
								<div class='spinner-border text-primary' role='status'>
									<span class='sr-only'></span>
								</div>
							)}
							{/* // {)}
							{} */}
						</tbody>
					</table>
				</div>
			</div>
		</div>
	);
}
