import React, { useState, useEffect } from 'react';
import axios from 'axios';

const host = 'http://localhost:8000';

const Places_Server = `${host}/places/`;

export default function PlacesComponent() {
	const [places, setPlaces] = useState([]);
	const [sortData, setSortData] = useState('place');
	const [sortOrder, setSortOrder] = useState(true);
	const [placeName, setPlaceName] = useState('');
	const [placeId, setPlaceId] = useState('');

	const getPlaces = async () => {
		const response = await axios.get(Places_Server);
		await setPlaces(response.data);
	};

	useEffect(() => {
		getPlaces();
	}, []);

	// useEffect(() => {
	// 	sortOrder === false
	// 		? setPlaces(places.sort((a, b) => a - b).reverse())
	// 		: setPlaces(places.sort((a, b) => a - b));
	// }, [sortData]);

	const sortvalue = async () => {
		if (sortOrder === false) {
			setPlaces(places.sort((a, b) => a.place_id - b.place_id).reverse());
			await setSortOrder(!sortOrder);
		} else {
			setPlaces(places.sort((a, b) => a.place_id - b.place_id));
			await setSortOrder(!sortOrder);
		}
	};

	const sortvaluePlaces = async () => {
		if (sortOrder === false) {
			setPlaces(places.sort((a, b) => a.place - b.place).reverse());
			await setSortOrder(!sortOrder);
		} else {
			setPlaces(places.sort((a, b) => a.place - b.place));
			await setSortOrder(!sortOrder);
		}
	};

	const deletePlace = async (id) => {
		await axios.delete(Places_Server + id);
		await getPlaces();
	};

	const savePlace = async () => {
		const data = {
			place_id: placeId,
			place: placeName,
		};
		await axios.post(Places_Server, data);
		await getPlaces();
		await setPlaceName('');
		await setPlaceId('');
	};

	return (
		<div>
			<div className='d-flex flex-end gap-3 p-3'>
				<p className='badge bg-dark'>Add Place</p>
				<input
					type='number'
					className='form-control w-25'
					placeholder='Place id'
					onChange={(e) => setPlaceId(e.target.value)}
					value={placeId}
				/>
				<input
					type='text'
					className='form-control w-25'
					placeholder='place name'
					onChange={(e) => setPlaceName(e.target.value)}
					value={placeName}
				/>
				<button className='btn btn-success' onClick={savePlace}>
					+
				</button>
			</div>
			<div
				style={{
					height: 400,
					overflow: 'auto',
				}}>
				<table className='table'>
					<thead>
						<tr className=''>
							<th onClick={sortvaluePlaces}>
								Place <i class='bi bi-sort-alpha-down'></i>
							</th>
							<th onClick={sortvalue}>
								Place Id <i class='bi bi-sort-alpha-down'></i>
							</th>
						</tr>
					</thead>
					<tbody>
						{places.map((data) => (
							<tr>
								<td>{data.place}</td>
								<td>{data.place_id}</td>
								<td>
									<button
										className='btn btn-danger'
										onClick={() => deletePlace(data.id)}>
										Delete
									</button>
								</td>
							</tr>
						))}
					</tbody>
				</table>
			</div>
		</div>
	);
}
