import React, { Component } from 'react';
import LOGO from './LOGO.jpg';
import './settings.css';
import RegisterComponent from '../register/Register';
import RefreshComponent from '../refresh/refresh';

export default class Settings extends Component {
	constructor(props) {
		super(props);
		this.state = {
			company: '',
		};
	}
	setCompany = () => {
		localStorage.setItem('company', this.state.company);
		window.location.reload();
	};

	componentDidMount() {
		const company = localStorage.getItem('company');
		this.setState({
			company,
		});
	}

	render() {
		return (
			<div className='d-flex flex-row justify-content-evenly'>
				<div className='d-flex flex-column gap-3'>
					<div class='d-flex flex-row gap-3'>
						<input
							type='text'
							onChange={(e) =>
								this.setState({
									company: e.target.value,
								})
							}
							className='form-control w-60'
							placeholder='Enter Company Name'
						/>
						<button onClick={this.setCompany} className='btn btn-primary w-20'>
							Save Company
						</button>
					</div>
					<h5>Company Name: {this.state.company}</h5>
					<h5>Software Version: 2.3.0</h5>
					<br />
					<RefreshComponent />
					<a className='btn btn-primary' href='/addPlace'>
						Add Place
					</a>
				</div>
				<div
					style={{ padding: 20 }}
					className='w-50 d-flex justify-content-center shadow-sm'>
					<RegisterComponent />
				</div>
			</div>
		);
	}
}
